.slick-list,.slick-slider,.slick-track{
    position:relative;display:block
}
.slick-loading .slick-slide,.slick-loading .slick-track{
    visibility:hidden
}
.slick-slider{
    box-sizing:border-box;
    -webkit-user-select:none;
    -moz-user-select:none;
    -ms-user-select:none;
    user-select:none;
    -webkit-touch-callout:none;
    -khtml-user-select:none;
    -ms-touch-action:pan-y;
    touch-action:pan-y;
    -webkit-tap-highlight-color:transparent
}
.slick-list{
    overflow:hidden;margin:0;padding:0
}
.slick-list:focus{
    outline:0
}
.slick-list.dragging{
    cursor:pointer;
    cursor:hand
}
.slick-slider .slick-list,.slick-slider .slick-track{
    -webkit-transform:translate3d(0,0,0);
    -moz-transform:translate3d(0,0,0);
    -ms-transform:translate3d(0,0,0);
    -o-transform:translate3d(0,0,0);
    transform:translate3d(0,0,0)
}
.slick-track{
    top:0;left:0
}
.slick-track:after,.slick-track:before{
    display:table;content:''
}
.slick-track:after{
    clear:both
}
.slick-slide{
    display:none;
    float:left;
    height:100%;
    min-height:1px
}
[dir=rtl] .slick-slide{
    float:right
}
.slick-slide img{
    display:block
}
.slick-slide.slick-loading img{
    display:none
}
.slick-slide.dragging img{
    pointer-events:none
}
.slick-initialized .slick-slide{
    display:block
}
.slick-vertical .slick-slide{
    display:block;height:auto;
    border:1px solid transparent
}
.slick-arrow.slick-hidden{
    display:none
}
.slick-dots,.slick-next,.slick-prev{
    position:absolute;
    display:block;padding:0
}
.slick-dots li button:before,.slick-next:before,.slick-prev:before{
    font-family:slick;
    -webkit-font-smoothing:antialiased;
    -moz-osx-font-smoothing:grayscale
}

@font-face{
    font-family:slick;
    font-weight:400;
    font-style:normal;
    src:url("/public/fonts/slick.eot");
    src:url("/public/fonts/slick.eot?#iefix") format('embedded-opentype'),url("/public/fonts/slick.woff") format('woff'),url("/public/fonts/slick.ttf") format('ttruetype'),url("/public/fonts/slick.svg") format('svg')
}
.slick-next,.slick-prev{
    font-size:0;line-height:0;top:50%;width:20px;height:20px;-webkit-transform:translate(0,-50%);-ms-transform:translate(0,-50%);transform:translate(0,-50%);cursor:pointer;color:transparent;border:none;outline:0;background:0 0
}
.slick-next:focus,.slick-next:hover,.slick-prev:focus,.slick-prev:hover{
    color:transparent;outline:0;background:0 0
}
.slick-next:focus:before,.slick-next:hover:before,.slick-prev:focus:before,.slick-prev:hover:before{
    opacity:1
}
.slick-next.slick-disabled:before,.slick-prev.slick-disabled:before{
    opacity:.25
}
.slick-next:before,.slick-prev:before{
    font-size:20px;line-height:1;opacity:.75;color:#fff
}
.slick-prev{left:-25px}[dir=rtl] .slick-prev{
    right:-25px;left:auto
}
.slick-prev:before{
    content:''
}
.slick-next:before,[dir=rtl] .slick-prev:before{
    content:''
}
.slick-next{
    right:-25px
}
[dir=rtl] .slick-next{
    right:auto;
    left:-25px
}
[dir=rtl] .slick-next:before{
    content:''
}
.slick-dotted.slick-slider{
    margin-bottom:30px
}
.slick-dots{
    bottom:-25px;width:100%;margin:0;list-style:none;text-align:center
}
.slick-dots li{
    position:relative;display:inline-block;width:20px;height:20px;margin:0 5px;padding:0;cursor:pointer
}
.slick-dots li button{
    font-size:0;line-height:0;display:block;width:20px;height:20px;padding:5px;cursor:pointer;color:transparent;border:0;outline:0;background:0 0
}
.slick-dots li button:focus,.slick-dots li button:hover{
    outline:0
}
.slick-dots li button:focus:before,.slick-dots li button:hover:before{
    opacity:1
}
.slick-dots li button:before{
    font-size:6px;line-height:20px;position:absolute;top:0;left:0;width:20px;height:20px;content:'';text-align:center;opacity:.25;color:#000
}
.slick-dots li.slick-active button:before{
    opacity:.75;color:#000
}
.container-experiencia {
    margin: 0 auto;
    width: 100%;
}

.experienciaDark{
    background-color:#302e2e;
    color: white;
}

.footerDark{
    background-color:#524f4f;
    color: white;
}

.experienciaLight{
    background-color:rgb(220, 233, 233);
    color: black;
}
.footerLight{
    background-color:rgb(197, 223, 223);
    color: black;
}

.experiencia_ {
    border-radius: 10px;
    -webkit-box-shadow: 0 0 5px 2px gray;
    -moz-box-shadow: 0 0 5px 2px gray;
    box-shadow: 0 0 5px 2px gray;
    text-align: center;
    font-size: 18px;
    padding-top: 10px;
    margin: 10px
}


.img-experiencia{
    width: 360px;
    margin: 20px auto;
}

.img-experiencia .img-ex2{
    display: inline-block;
}
.img-experiencia .img-ex2-mas{
    display: inline-block;
}

.img-ex2{
    border: 2px solid #6A98F0;
    position: relative;
    margin: 10px;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    padding: 5px 5px 5px 5px;
    overflow:hidden;
}
.img-ex2 img{
    display:block;
    max-width:100%;
    height:auto;
}
.img-ex2-mas{
    position: relative;
    margin: 10px;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    padding: 5px 5px 5px 5px;
    overflow:hidden;
}
.img-ex2-mas img{
    display:block;
    max-width:100%;
    height:auto;
}
.footer-experiencia_{
    width: 100%;
    height: 200px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}


@media screen and (max-width: 850px) {
    .img-experiencia{
        width: 350px;
    }
}